import React,{useContext,useState} from 'react'
import {useHistory,Link} from "react-router-dom"
import { auth } from 'firebase';
import {Context} from "../../Context"
import "./Header.scss"
import Button from '../Button/Button'
import ShoppingCart from "../ShoppingCart/ShoppingCart"
import Logo from '../Logo/Logo';
import Search from '../Search/Search';


export default function Header({toggle,Cart}) {
    const {user,cart} = useContext(Context)
    let history = useHistory();
    
    const handleSignOut=async ()=>{
        auth().signOut();
        user.set("")
        if (!user.get){
            history.push("/")
        }
    }
    return(
        <div className="header">
            <div className="logo" onClick={()=>history.push("/")}>
            <Logo/>
            </div>
            <div className="actions">
                {Cart}<span className="cartCounter">{cart.get.length?cart.get.length:null}</span>
                {toggle? null:<ShoppingCart/>}
                
            <div className="buttonWrapper">
                {user.get?
                    <Button Buttontype="primary" content="SignOut" onClick ={handleSignOut}/>
                    :<Button Buttontype="primary" content="Login" onClick={()=>history.push("/Login")}/>}
            </div>
            </div>
        </div>
    )
}

